.Key {
  display: inline-block;
  width: 100%;
  height: 4em;
  position: relative;
  border-radius: 0.5em;
  margin-top: 0;
  margin-bottom: 0.4em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Key--pressed {
  margin-top: 0.4em;
  margin-bottom: 0;
}

@media (pointer: coarse) {
  .Key:active {
    margin-top: 0.4em;
    margin-bottom: 0;
  }
}

.Key-symbol {
  width: 1em;
  height: 1em;
}

.Key-symbol path {
  fill: currentColor;
}

.KeyWin {
  color: #eee;
  background-color: #555;
  text-shadow: 0 -0.1em 0 hsla(0, 0%, 0%, 0.5);
  box-shadow:
    inset 0 0 0 0.1em hsla(0, 0%, 0%, 0.5),
    inset 0 0 0 0.2em hsla(0, 0%, 100%, 0.125),
    0 0.3em 0 #222,
    -0.1em 0.5em 0 #000,
    0.1em 0.5em 0 #000;
}

.KeyWin.Key--pressed {
  box-shadow:
    inset 0 0 0 0.1em hsla(0, 0%, 0%, 0.5),
    inset 0 0 0 0.2em hsla(0, 0%, 100%, 0.125),
    -0.1em 0.1em 0 #000,
    0.1em 0.1em 0 #000;
}

@media (pointer: coarse) {
  .KeyWin:active {
    box-shadow:
      inset 0 0 0 0.1em hsla(0, 0%, 0%, 0.5),
      inset 0 0 0 0.2em hsla(0, 0%, 100%, 0.125),
      -0.1em 0.1em 0 #000,
      0.1em 0.1em 0 #000;
  }
}

.KeyWin > .Key-text {
  position: absolute;
  top: 0.5em;
  left: 0.5em;
}

.KeyWin > .Key-symbol {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-style: italic;
}

.KeyMac {
  color: #555;
  background-color: #eee;
  text-transform: lowercase;
  text-shadow: 0 0.1em 0 #fff;
  box-shadow:
    inset 0 0 0 0.1em hsla(0, 0%, 0%, 0.25),
    inset 0 0 0 0.2em hsla(0, 0%, 100%, 0.5),
    0 0.3em 0 #ccc,
    -0.1em 0.5em 0 #888,
    0.1em 0.5em 0 #888;
}

.KeyMac.Key--pressed {
  box-shadow:
    inset 0 0 0 0.1em hsla(0, 0%, 0%, 0.25),
    inset 0 0 0 0.2em hsla(0, 0%, 100%, 0.5),
    -0.1em 0.1em 0 #888,
    0.1em 0.1em 0 #888;
}

@media (pointer: coarse) {
  .KeyMac:active {
    box-shadow:
      inset 0 0 0 0.1em hsla(0, 0%, 0%, 0.25),
      inset 0 0 0 0.2em hsla(0, 0%, 100%, 0.5),
      -0.1em 0.1em 0 #888,
      0.1em 0.1em 0 #888;
  }
}

.KeyMac > .Key-text {
  position: absolute;
  bottom: 0.5em;
  left: 0.5em;
}

.KeyMac > .Key-symbol {
  position: absolute;
  top: 0.5em;
  right: 0.5em;
}

.Modifiers th {
  text-align: left;
  padding: 1em 0 1em 0.5em;
  font-size: 0.8em;
  font-weight: normal;
  color: #888;
}

.Modifiers td {
  padding: 0.125em 0.25em;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

html {
  font-size: 10px;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: sans-serif;
  font-size: 2em;
  font-size: min(2em, 3vw);
}

